@use 'src/scss/core' as *;
@use 'src/scss/mixins/modals' as *;

.input-modal {
  @include modal;

  flex-direction: column;
  align-items: center;
  width: 60vmin;
  height: 25vmin;

  * {
    color: $nord6;
  }

  .text {
    width: 80%;
    margin: 6vmin auto 3vmin;
    border: unset;
    outline: unset;
    background: unset;
    font-size: 3.5vmin;
    text-align: center;
  }

  .submit {
    @include styled-box($small-styled-box-config, $nord15, 5vmin, 5vmin);
  }

  .disabled {
    color: $nord3;
    text-decoration: line-through;

    &:hover {
      cursor: not-allowed;
    }
  }
}
